.fixedsticky {
  top: 0; }

.cat-icon {
  max-width: 50px;
  padding: 0 5px; }

.news-top-holder {
  background: #f7f7f7;
  overflow: scroll;
  box-shadow: rgba(73, 73, 73, 0.2) 0px 2px 6px 0px; }

.wrap {
  padding-bottom: 100px; }

.nav-links .nav-previous {
  display: inline-block;
  float: right; }
  .nav-links .nav-previous a:after {
    content: " \2192";
    padding-right: 5px; }

.nav-links .nav-next {
  display: inline-block;
  float: left; }
  .nav-links .nav-next a:before {
    content: "\2190 ";
    padding-left: 5px; }

.news-top {
  width: 1000px; }
  .news-top .row {
    padding: 0; }
  @media (min-width: 1200px) {
    .news-top {
      width: 1230px; } }
  .news-top img {
    /* IE6-9 */
    /* Microsoft Edge and Firefox 35+ */
    /* Google Chrome, Safari 6+ & Opera 15+ */ }
  .news-top h2, .news-top .related_post .wp_rp_title, .related_post .news-top .wp_rp_title {
    font-size: 16px;
    margin-top: 10px; }
  .news-top .post-meta-container, .news-top .entry-summary {
    display: none; }
  .news-top .cat-icon-container {
    float: left; }
  .news-top .cat-icon {
    max-width: 30px;
    float: left;
    padding-bottom: 50px; }

.feed-content {
  background-size: cover; }

.btn-circle {
  width: 30px;
  height: 30px;
  text-align: center;
  padding: 6px 0;
  font-size: 12px;
  line-height: 1.428571429;
  border-radius: 50%; }

.btn-circle.btn-lg {
  width: 50px;
  height: 50px;
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.33;
  border-radius: 50%; }

.btn-circle.btn-xl {
  width: 70px;
  height: 70px;
  padding: 10px 16px;
  font-size: 24px;
  line-height: 1.33;
